<template>
  <div class="app-container">
    <el-button type="primary" style="margin-bottom: 10px"
               @click="dialogAddVisible = true">添加
    </el-button>
    <el-table
        ref="filterTable"
        :data="tableData"
        v-loading="listLoading"
        border
        style="width: 100%">
      <!--编号-->
      <el-table-column
          prop="id"
          label="编号"
          sortable
          width="100px"
      >
      </el-table-column>
      <!--分类-->
      <el-table-column
          prop="name"
          label="分类">
      </el-table-column>
      <!--操作-->
      <el-table-column
          label="操作"
          width="180">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" icon="el-icon-edit"
                     @click.native.prevent="updateCategory(scope.row)"></el-button>
          <el-button type="danger" size="mini" icon="el-icon-delete"
                     @click.native.prevent="deleteRow(scope.$index, tableData,scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="添加分类" :visible.sync="dialogAddVisible">
      <el-form ref="form" :model="form" :rules="rule">
        <el-form-item label="分类" prop="name">
          <el-input v-model="form.name" ref="name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCategory('form')">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="修改分类" :visible.sync="dialogUpdateVisible">
      <el-form ref="updateForm" :model="updateForm" :rules="rule">
        <el-form-item label="分类" prop="name">
          <el-input v-model="updateForm.name" ref="updateName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogUpdateVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateCategorySubmit('updateForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {getCategoryApi,addCategoryApi,updateCategoryApi} from "@/api/article";


export default {
  name: "Category",
  data() {
    return {
      listLoading: true,
      total: 0,
      tableData: [],
      dialogAddVisible: false,
      dialogUpdateVisible:false,
      form: {
        name: ''
      },
      updateForm:{
        id:undefined,
        name:undefined
      },
      rule: {
        name: [
          {required: true, message: '请输入分类', trigger: 'blur'},
          {min: 1, max: 10, message: '长度在 1 到 10个字符', trigger: 'blur'}
        ]
      },
      row:undefined
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.listLoading = true
      getCategoryApi().then(res => {
        this.tableData = res.data
        setTimeout(() => {
          this.listLoading = false
        }, 1.5 * 1000)
      })
    },
    addCategory(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let res =await addCategoryApi(this.form)
          this.tableData.unshift(res.data)
          this.form.name=""
        } else {
          this.$refs.name.focus()
          return false
        }
      })
    },
    updateCategory(value) {
      this.dialogUpdateVisible=true
      this.updateForm.id=value.id
      this.updateForm.name=value.name
      this.row=value
    },
    updateCategorySubmit(formName){
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          updateCategoryApi(this.updateForm).then(res=>{
            //console.log(res)
            this.dialogUpdateVisible=false
            this.row.name=res.data.name
          })
        } else {
          this.$refs.name.focus()
          return false
        }
      })
    },
    deleteRow(index, rows) {
      this.$confirm('确定要删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        rows.splice(index, 1);
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      })
    }
  }
}
</script>

<style scoped>

</style>
